.search-site {
    padding-bottom: 60px;
    .section-title {
        margin-bottom: 30px;
    }
    .search_block .input {
        flex-shrink: 1;
    }
    &__result {
        margin-top: 15px;
    }
    &__item {
        font-size: 13px;
        line-height: 19px;
        &:not(:last-of-type) {
            margin-bottom: 35px;
        }
        &-title {
            display: inline-block;
            margin: 0 0 10px;
            font-size: 13px;
            line-height: 19px;
            text-decoration: none;
            text-transform: uppercase;
            font-weight: 600;
            color: var(--mainColor);
            transition: all 0.2s;
            &:hover {
                color: var(--Wedgewood);
            }
        }
        &-info {
            margin: 0 0 13px;
            font-size: 13px;
            line-height: 19px;
        }
        &-linkpath {
            font-size: 13px;
            line-height: 19px;
            a {
                display: inline-block;
                &:not(:last-child) {
                    &:after {
                        content: "/";
                        display: inline-block;
                        margin: 0 0 0 4px;
                    }
                }
            }
        }
        &-href {
            color: var(--Flamenco);
        }
    }
    &__sort-block {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-top: 5px;
    }
    &__sort {
        display: inline-block;
        font-size: 14px;
        button {
            color: var(--Wedgewood);
        }
    }
    button.search-site__sort-active {
        color: var(--mainColor);
        border-bottom: 1px dashed var(--mainColor);
    }
    &__sort-date {
        overflow: visible;
        &:before {
            content: "|";
            display: inline-block;
            margin: 0 6px 0 4px;
            color: var(--mainColor);
        }
    }
    .pagination {
        margin: 0;
    }
}
@media(max-width: 810px) {
    .search-site {
        padding-bottom: 30px;
        &__sort-block {
            margin-top: -5px;
            align-items: flex-start;
        }
        &__sort {
            form {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
            }
            &-date {
                position: relative;
                display: block;
                width: 100%;
                margin-top: 7px;
                text-align: left;
                &:before {
                    position: absolute;
                    right: -11px;
                    top: calc(-100% - 7px);
                    margin: 0;
                }
            }
        }
    }
}
@media(max-width: 767px) {
    .search-site {
        &__result {
            margin-top: 8px;
        }
        &__item {
            &:not(:last-of-type) {
                margin-bottom: 23px;
            }
        }
    }
}
@media(max-width: 640px) {
    .search-site {
        &__sort-block {
            flex-direction: column;
            align-items: flex-start;
            margin-top: 24px;
        }
        .pagination {
            width: 100%;
            justify-content: center;
            margin: 28px 0 0;
        }
    }
}
@media(max-width: 575px) {
    .search-site {
        &__item {
            &-title {
                font-size: 15px;
                line-height: 23px;
            }
        }
        .pagination {
            margin: 28px 0 0;
        }
    }
}